.login {
  display: grid;
  place-items: center;
  height: 100vh;
  background-color: white;
}

.login > img {
  width: 400px;
  height: 400px;
}

.login > a {
  padding: 20px;
  border-radius: 99px;
  font-weight: 600;
  color: black;
  text-decoration: none;
  background-color: white;
}
